/*
 * CSS Reset
 * A modern CSS reset inspired by Andy Bell's
 * https://andy-bell.co.uk/a-modern-css-reset/
 */
/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin and padding */
html,
body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
figure,
blockquote,
dl,
dd,
ul,
ol {
  margin: 0;
  padding: 0;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
  font-family: "Inter", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
  font-size: 1rem;
  color: #212529;
  background-color: #ffffff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Remove list styles on ul, ol elements */
ul,
ol {
  list-style: none;
}

/* Set default text styles for headings and paragraphs */
h1, h2, h3, h4, h5, h6 {
  font-family: "Inter", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
}

p {
  margin-bottom: 1rem;
}

/* Make images easier to work with */
img,
picture,
svg {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people who prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
/* Set core anchor defaults */
a {
  text-decoration: none;
  color: #f97116;
  transition: all 0.2s ease-in-out;
}
a:hover {
  color: rgb(214.4769874477, 89.2887029289, 5.5230125523);
}

/* Remove border on focused elements */
:focus {
  outline: none;
}

/* Focus visible utility */
:focus-visible {
  outline: 2px solid #f97116;
  outline-offset: 2px;
}

/* Table formatting */
table {
  border-collapse: collapse;
  width: 100%;
}

th {
  text-align: left;
}

/* Form element defaults */
button {
  cursor: pointer;
  border: none;
  background: none;
  padding: 0;
}

fieldset {
  border: none;
  padding: 0;
  margin: 0;
}

select {
  appearance: none;
}

