a.text-link::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  bottom: -2px;
  left: 0;
  background-color: currentColor;
  transform: scaleX(0);
  transform-origin: right;
  transition: transform 0.3s ease;
}
a.text-link:hover::after, a.text-link:focus::after {
  transform: scaleX(1);
  transform-origin: left;
}

